    <a v-for ="menu in menus" :key="menu" @click="sale = menu"> {{menu}} </a>
  </div>
  
  <!-- 문제> Event를 클릭하면 디스카운트 템플릿이 Nav 바 밑에 애니메이션으로 나타나도록
    숙제>버튼 명에 대해서 내림차순 오름차순
   -->

  <!-- <div class="discount">
    <h4>
      지금 결제하면 20% 할인
    </h4>
  </div> -->
  <transition name="saleEvent">
    <Discount  v-if="sale == 'Event'"/>
  </transition>

  <button @click="sortPriceAsc">가격 오름차순 정렬</button>
  <button @click="sortPriceDesc">가격 내림차순 정렬</button><br>
  <button @click="sortNameAsc">이름 오름차순 정렬</button>
  <button @click="sortNameDesc">이름 내림차순 정렬</button><br>
  <button @click="dataOrigin">원래대로</button>

  <!-- <div class="black-bg" v-if="modalStatus == true">
    <div class="white-bg">
      <img class="room-img" :src="roomsInfo[currentProductNo].image">
      <h4>{{roomsInfo[currentProductNo].title}}</h4>
      <p>{{roomsInfo[currentProductNo].content}}원</p>
      <p>{{roomsInfo[currentProductNo].price}}원</p>
      <button @click="modalStatus = false">닫기</button>
    </div>
  </div> -->
  <!-- <div class="aniStart" :class="{aniEnd : modalStatus}">
    <Modal @closeModal = "modalStatus = false" @resetReport = "신고수[$event] = 0" :roomsInfo = "roomsInfo" :currentProductNo = "currentProductNo"
    :modalStatus = "modalStatus"
    :신고수 = "신고수"/>
  </div> -->
  <transition name="fade">
    <Modal @closeModal = "modalStatus = false" @resetReport = "신고수[$event] = 0" :roomsInfo = "roomsInfo" :currentProductNo = "currentProductNo"
    :modalStatus = "modalStatus"
    :신고수 = "신고수"/>
  </transition>
  
  

  <!-- <div v-for = "(room,i) in roomsInfo" :key = "i">
    <img class="room-img" :src="room.image">
    <h4 @click="modalStatus = true; currentProductNo  = room.id">{{room.title}}</h4>
    <p>{{room.price}}원</p>
    <button @click="신고수[i]++">허위 매물 신고</button>
    <span>신고수 : {{ 신고수[i] }}</span>
  </div> --> 
  <Card @openModal="modalStatus = true; currentProductNo  = $event"
   @report = "신고수[$event]++" 
   v-for = "room in roomsInfo" :key = "room"
  :room = "room" 
  :modalStatus = "modalStatus"
  :신고수 = "신고수" 
  />

  
</template>

<script>
import data from "./assets/oneroom.js"
import Discount from "./components/Discount.vue"
import Modal from "./components/Modal.vue"
import Card from "./components/Card.vue"
export default {
  name: 'App',
  data(){
    return{
      sale : '',
      modalStatus : false,
      currentProductNo : 0,
      origin : [...data],
      roomsInfo : data,
      price : [50,60,70],
      products : ['역삼동 원룸','천호동 원룸', '마포구 원룸'],
      menus : ['Home','Shop','Event'],
      신고수 : [0,0,0,0,0,0],
    }
  },//외우자
  components: {
    Discount,
    Modal,
    Card,
  },
  methods : {
    //숫자 정렬
    // sortTest(){
    //     let arr = [ 3 , 5 , 2 ];
    //     arr.sort();
    //     console.log('오름차순 정렬 : ' + arr);

    //     arr.sort(function(a,b){
    //       return b-a;
    //     });
    //     console.log('내림차순 정렬 : ' + arr);
    // },

    sortPriceAsc(){
      this.roomsInfo.sort(function(a,b){
        return a.price - b.price;
      });
    },
    sortPriceDesc(){
      this.roomsInfo.sort(function(a,b){
        return b.price - a.price;
      })
    },
    sortNameAsc(){
      this.roomsInfo.sort((a,b)=>{
        if(a.title < b.title) return -1;
        if(a.title > b.title) return 1;
        else return 0;      
      })
    },
    sortNameDesc(){
      this.roomsInfo.sort(function(a,b){
        if(a.title > b.title) return -1;
        if(a.title < b.title) return 1;
        else return 0;
      })
    },
    dataOrigin(){
      this.roomsInfo = [...this.origin];
    },
  }
}
</script>

